<template>
    <div class="top">
      <div class="inputBox">
        <form action="#">
          <i class="iconfont icon-sousuo"></i>
          <input type="text" class="searchBox" :placeholder="placeholder" v-model="query" @click="quxiao" ref="query">
          <i class="iconfont icon-guanbi" v-show="query" @click="clear"></i>
        </form>

      </div>
      <div class="quxiao" v-if="show||query" @click="xianshi">取消</div>
    </div>
</template>

<script>
  import {debounce} from '../common/js/util'
    export default {
        name: "searchBox",
      props: {
        placeholder: {
          type: String,
          default: '搜索歌曲、歌手'
        },
        offShow:{
          type:Boolean,
          default: true
        }
      },
      data(){
          return {
            query: '',
            show:false,
            onshow:false
          }
      },
      created(){
        this.$watch('query', debounce((newQuery) => {
          this.$emit('query', newQuery)
        }, 200))
      },
      methods:{
        clear(){
          this.query=''
        },
        quxiao(){
          this.show=true
          this.offshow=false
          this.$emit('shows',this.offshow)
        },
        xianshi(){
          this.show=false
          this.offshow=true
          this.$emit('onshows',this.offshow)
          this.query=''
        },
        blur(){
            this.$refs.query.blur()
        },
        setQuery(query){
          this.query = query
        }
      }
    }
</script>

<style scoped lang="stylus">
  .top{
    position: relative
    top:0
    display: flex
    width:100%
    height: 56px
    align-items :center
    background: #f4f4f4;
    padding: 10px;
    .inputBox{
      position: relative;
      border-radius: 10px;
      background: #fff;
      height: 36px;
      padding: 8px 12px 8px 35px;
      flex:1
      align-items :center
      .searchBox{
        height: 20px;
        line-height: 20px;
        width: 100%;
        color: rgba(0,0,0,.3);
        border: none;
        font-size: 14px;
        outline:none
        outline-offset: 5px;
      }
      .icon-sousuo{
        position: absolute;
        top: 8px;
        left: 10px;
        width: 18px;
        height: 18px;
        font-size:22px
        color:rgba(0,0,0,.3)
      }
      .icon-guanbi{
        position: absolute;
        top: 8px;
        right: 12px;
        width: 18px;
        height: 18px;
        border-radius: 99px;
        font-size:20px
        color:rgba(0,0,0,.3)
      }

    }
    .quxiao{
      padding-right: 10px;
      padding-left: 10px;
      font-size: 14px;
      height: 36px;
      line-height: 36px;
    }
  }
</style>



