<template>
    <div class="searchList" v-show="searches.length">
      <transition-group name="list" tag="ul">
        <li :key="item" class="searchItem" v-for="item in searches" @click="selectItem(item)">
          <span class="history">
            <i class="iconfont icon-lishijilu1"></i>
          </span>
          <span class="text">
            {{item}}
          </span>
          <span class="clear">
            <i class="iconfont icon-shanchu1" @click.stop="deleteOne(item)"></i>
          </span>
        </li>
      </transition-group>
    </div>
</template>

<script>
    export default {
        name: "searchList",
      props:{
          searches:{
            type:Array,
            default:[]
          }
      },
      methods:{
          selectItem(item){
            this.$emit('select',item)
          },
        deleteOne(item){
            this.$emit('delete',item)
        }
      }
    }
</script>

<style scoped lang="stylus">
  .searchList{
    .searchItem{
      height:30px
      line-height :20px
      color: rgba(0,0,0,.6);
      display: flex
      align-items :center
      font-size :14px
      .history{
        flex:0 0 20px
        color: rgba(0,0,0,.6)
      }
      .text{
        flex :1
      }
      .clear{
        flex:0 0 40px
      }
    }

  }
  .list-enter-active,.list-leave-active{
    transition:all .1s
  }
  .list-enter,.list-leave-to{
    height:0
  }

</style>



