<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" @click="hide">
      <div class="list-wrapper" @click.stop>
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="iconMode" @click="changeMode"></i>
            <span class="listBtn">{{modeText}}</span>
            <div class="list-operate">
              <div class="add" @click="addSong">
                <i class="iconfont icon-tianjia1"></i>
                <span >添加歌曲</span>
              </div>
            </div>
            <span class="clear" @click="showConfirm"><i class="iconfont icon-shanchu"></i></span>
          </h1>
        </div>
        <Scroll :data="sequenceList" ref="listContent" class="list-content" :refreshDelay="refreshDelay">
          <transition-group ref="list" name="list" tag="ul">
            <li :key="item.id" class="item" v-for="(item,index) in sequenceList" @click="selectItem(item,index)" ref="listItem">
              <i class="current" :class="getCurrentIcon(item)"></i>
              <div class="text" :class="getCurrentText(item)">
              <span>{{item.name}}</span>
              <span class="singername" :class="getCurrentText(item)">- {{item.singer}}</span>
              </div>
              <span class="like" @click.stop="toggleFavorite(item)">
                <i class="iconfont" :class="getFavoriteIcon(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteOne(item)">
                <i class="iconfont icon-shanchu1"></i>
              </span>
            </li>
          </transition-group>
        </Scroll>

        <div @click="hide" class="list-close">
          <span>关闭</span>
        </div>
      </div>
      <Confirm ref="confirm" @confirm="confirmClear" text="是否清空播放列表" confirmBtnText="清空"></Confirm>
      <AddSong ref="addSong"></AddSong>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {mapActions} from 'vuex'
  import {playMode} from '../../common/js/config'
  import Scroll from '../../base/scroll'
  import Confirm from '../../base/confirm'
  import AddSong from '../add-song/add-song'
  import {playerMixin} from '../../common/js/mixin'

  export default {
       mixins:[playerMixin],
        data(){
          return {
            showFlag:false,
            refreshDelay:100
          }
        },
    components:{
          Scroll,
          Confirm,
          AddSong
    },
    computed:{
      modeText(){
        return this.mode === playMode.sequence ? '顺序播放' : this.mode === playMode.random ? '随机播放' : '单曲循环'
      }
    },
    methods:{
          show(){
            this.showFlag=true
            setTimeout(() => {
              this.$refs.listContent.refresh()
              this.scrollToCurrent(this.currentSong)
            }, 20)
          },
          addSong(){
            this.$refs.addSong.show()
          },
          hide(){
            this.showFlag=false
          },
          ...mapActions([
            'deleteSong',
            'deleteSongList'
          ]),
          showConfirm(){
            this.$refs.confirm.show()
          },
          confirmClear(){
              this.deleteSongList()
          },
          deleteOne(item){
              this.deleteSong(item)
              if (!this.playlist.length) {
                this.hide()
              }
          },
          scrollToCurrent(current) {
            const index = this.sequenceList.findIndex((song) => {
            return current.id === song.id
        })
            this.$refs.listContent.scrollToElement(this.$refs.list.$el.children[index], 300)
      },
          getCurrentIcon(item){
            if(this.currentSong.id===item.id){
                return 'iconfont icon-tubiaozhizuomoban'
            }
          },
          getCurrentText(item){
            if(this.currentSong.id===item.id){
              return 'colorText'
            }
          },
          selectItem(item, index) {
            if (this.mode === playMode.random) {
                index = this.playList.findIndex((song) => {
                return song.id === item.id
              })
            }
            this.setCurrentIndex(index)
            this.setPlayingState(true)
          },
    },
    watch: {
      currentSong(newSong, oldSong) {
        if (!this.showFlag || newSong.id === oldSong.id) {
          return
        }
        setTimeout(() => {
          this.scrollToCurrent(newSong)
        }, 20)
      }
    },
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: rgba(0,0,0,0.5)
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background-color: #FFFFFF
      opacity :0.95
      border-top-left-radius:10px;
      border-top-right-radius:10px;
      .list-header
        position: relative
        border-bottom:1px solid rgba(0,0,0,0.1)
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .listBtn
            flex:1
            color:#000
            font-size:15px
          .icon
            margin-right: 10px
            font-size: 28px
            color: rgba(0,0,0,0.3)
          .text
            font-size: 14px
            color:rgba(0,0,0,0.6)
          .clear
            extend-click()
            .icon-shanchu
              font-size: 20px
              color: rgba(0,0,0,0.3)
      .list-content
        max-height: 300px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 0
          overflow: hidden
          border-bottom:1px solid rgba(0,0,0,0.1)
          margin-left:7px
          &.item:last-child
            border:none
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s linear
          &.list-enter, &.list-leave-to
            height: 0
          .current
            font-size: 20px
            color: #00A7E1
            margin-right:4px
          .text
            flex: 1
            no-wrap()
            font-size: 15px
            color: #000
            .singername
              color:rgba(0,0,0,0.3)
              font-size :13px
          .colorText
              color: #00A7E1 !important
          .like
            extend-click()
            margin-right: 15px
            font-size: 14px
            color: rgba(0,0,0,0.3)
            .iconfont
              font-size:20px
              color: rgba(0,0,0,0.3)
            .icon-shoucang1
              color:#fc4524
          .delete
            extend-click()
            font-size: 14px
            color: rgba(0,0,0,0.3)
            .icon-shanchu1
              font-size:20px
              color: rgba(0,0,0,0.3)
      .list-operate
        margin-right: 10px
        text-align :right

        .add
          display: flex
          align-items: center
          border-right: 1px solid rgba(0,0,0,0.1)
          color: #000
          padding-right:10px
          .icon-tianjia1
            margin-right: 5px
            font-size: 18px
            color: rgba(0,0,0,0.3)
          .text
            font-size: 12px
          span
            font-size:15px
      .list-close
        text-align: center
        line-height: 50px
        background: #FFFFFF
        font-size: 15px
        color: rgba(0,0,0,0.6)
        border-top:1px solid rgba(0,0,0,0.1)
</style>



