<template>
  <div class="countBox">
    <ul>
      <li @click="selectItem(item,index)" v-for="(item,index) in songs">
        <div class="rank" v-show='rank' :class="getRankCls(index)">
          <span >{{index+1}}</span>
        </div>
        <div class="textContent">
        <h2 class="songName" v-html="item.name"></h2>
        <span v-text="item.singer+'·'+item.album"></span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "songList",
      props:{
          songs:{
            type:Array,
            default:[]
          },
          rank:{
            type:Boolean,
            default: false
          }
      },
      methods:{
        selectItem(item,index){
          this.$emit('select',item,index)
        },
        getRankCls(index){
          if(index<=2){
            return 'icon'
          }else{
            return 'gray'
          }
        }
      }
    }
</script>

<style scoped lang="stylus">
  .countBox {
    padding-top: 20px
    position: relative
    bottom: 0
    width: 100%
    z-index:1
    li{
      display: flex
      align-items :center
      border-bottom :1px solid rgba(222,222,222,.2)
      .rank{
        width:10px
        margin-left: 20px

        text-align :center
      }
      .icon{
        color:#FF815E
      }
      .gray{
        color:#A5A5A5
      }
    .textContent{
        height:62px
        display: flex
        flex-direction :column
        justify-content :center
        padding:10px 20px
        width:320px
      h2{
        height:24px
        line-height :24px
        color: #000
        overflow :hidden
        white-space :nowrap
        text-overflow :ellipsis
        width :200px
      }
      span{
        height:20px
        font-size :14px
        line-height :20px
        color: rgb(122,122,122)
        white-space: nowrap;
        text-overflow: ellipsis
        overflow: hidden
      }
    }
    }
  }
</style>



