<template>
    <div class="noResult">
      <img src="./loading/1.png">
      <p class="desc">{{title}}</p>
    </div>
</template>

<script>
    export default {
        name: "noResult",
      props:{
        title:{
            type:String,
            default:'暂无搜索结果'
          }
      }
    }
</script>

<style scoped lang="stylus">
  .noResult{
    position: absolute
    top: 40%
    left :50%
    transform :translate3d(-50%,-50%,0)
    display: flex
    flex-direction :column
    align-items :center
    img{
      width:150px
      height:150px
      border-radius :50%
    }
    .desc{
      height:20px
      color: rgba(0,0,0,0.7)
      line-height :20px
      text-align :center
      margin-left:20px
    }
  }

</style>



