<script>
  import {mapGetters,mapMutations,mapActions} from 'vuex'
  import animations from 'create-keyframe-animation'
  import Progressbar from '../../base/progressbar'
  import ProgressCircle from '../../base/progressCircle'
  import {playMode} from '../../common/js/config'
  import {shuffle} from '../../common/js/util'
  import Lyric from 'lyric-parser'
  import Scroll from '../../base/scroll'
  import playList from '../../components/playList/playlist'
  import {getLyrics} from '../../api/song'
    export default {
        name: "player",
      data(){
        return{
          songReady:false,
          currentTime: 0,
          currentLyric:null,
          currentLineNum:0,
          show:true,
          swiperOption: {
            pagination: {
              el: '.swiper-pagination',
              clickable :true,
            },
            effect: 'fade',
            fadeEffect: {
              crossFade: true,
            }
          },
          playingLyric: '',
          state:0
        }
      },
      components:{
        Progressbar,
        ProgressCircle,
        Scroll,
        playList
      },
      computed:{
        ...mapGetters([
          'fullScreen',
          'playList',
          'currentSong',
          'playing',
          'currentIndex',
          'mode',
          'sequenceList',
          'favoriteList'
        ]),
        swiper() {
          return this.$refs.mySwiper.swiper
        },
        iconMode(){
          return this.mode === playMode.sequence?'iconfont icon-xunhuanbofang':this.mode=== playMode.loop?'iconfont icon-danquxunhuan':'iconfont icon-suijibofang1'
        },

        cdl(){
          return this.playing?'play':'play pause'
        },
        playIcon(){
          return this.playing?'iconfont icon-zanting':'iconfont icon-bofang1'
        },
        miniPlayIcon(){
          return this.playing?'iconfont icon-zanting-xianxingyuankuang':'iconfont icon-bofang'
        },
        disableCls(){
           return this.songReady ?'':'disable'
        },
        percent(){
          return this.currentTime/this.currentSong.duration
        }
      },
      methods:{
          back(){
            this.setFullScreen(false)

          },
        toggleFavorite(song) {
          if (this.isFavorite(song)) {
            this.deleteFavoriteList(song)
          } else {
            this.saveFavoriteList(song)
          }
        },
        getFavoriteIcon(song) {
          if (this.isFavorite(song)) {
            return 'icon-shoucang1'
          }
          return 'icon-htmal5icon38'
        },
        isFavorite(song) {
          const index = this.favoriteList.findIndex((item) => {
            return item.id === song.id
          })
          return index > -1
        },
        showPlayList(){
            this.$refs.playList.show()
        },
        changeMode(){
            const mode = (this.mode+1)%3
          this.setPlayMode(mode)
          let list = null
          if(mode===playMode.random){
              list = shuffle(this.sequenceList)
          }else{
            list = this.sequenceList
          }
          this.resetCurrentIndex(list)
          this.setPlaylist(list)
        },
        resetCurrentIndex(list){
            let  index = list.findIndex((item)=>{
              return item.id===this.currentSong.id
            })
          this.setCurrentIndex(index)
        },
          open(){
            this.setFullScreen(true)
          },
         enter(el, done){
           const {x, y, scale} = this._getPosAndScale()
           let animation = {
             0: {
               transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
             },
             60: {
               transform: `translate3d(0,0,0) scale(1.1)`
             },
             100: {
               transform: `translate3d(0,0,0) scale(1)`
             }
           }
           animations.registerAnimation({
             name: 'move',
             animation,
             presets: {
               duration: 400,
               easing: 'linear'
             }
           })

           animations.runAnimation(this.$refs.cdWrapper, 'move', done)
         },
        afterEnter() {
          animations.unregisterAnimation('move')
          this.$refs.cdWrapper.style.animation = ''
        },
        leave(el, done) {
          const {x, y, scale} = this._getPosAndScale()
          let animation = {
            100: {
              transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
            }
          }
          animations.registerAnimation({
            name: 'leave',
            animation,
            presets: {
              duration: 400,
              easing: 'linear'
            }
          })
          // this.$refs.cdWrapper.addEventListener('transitionend', done)
          // this.$refs.normalPlay.style.display='none'
          animations.runAnimation(this.$refs.cdWrapper,'leave', done)
        },
        afterLeave() {
          animations.unregisterAnimation('leave')
          this.$refs.cdWrapper.style.animation = ''

        },
        togglePlaying(){
          if(!this.songReady){
            return
          }
            this.setPlayingState(!this.playing)
          if (this.currentLyric) {
            this.currentLyric.togglePlay()
          }
        },
        next(){
            if(!this.songReady){
              return
            }
            if(this.playList.length===1){
              this.loop()
              return
            }else{
            let index = this.currentIndex +1
            if(index===this.playList.length){
              index = 0
            }
            this.setCurrentIndex(index)
          if(!this.playing){
            this.togglePlaying()
          }
            }
          this.songReady=false
        },
        prev(){
          if(!this.songReady){
            return
          }
          if(this.playList.length===1){
            this.loop()
            return
          }else {
            let index = this.currentIndex - 1
            if (index === -1) {
              index = this.playList.length - 1
            }
            this.setCurrentIndex(index)
            if (!this.playing) {
              this.togglePlaying()
            }
          }
          this.songReady=false
        },
        loop() {
          this.$refs.audio.currentTime = 0
          this.$refs.audio.play()
          this.setPlayingState(true)
          if (this.currentLyric) {
            this.currentLyric.seek(0)
          }
        },
        end() {
          if (this.mode === playMode.loop) {
            this.loop()
          } else {
            this.next()
          }
        },
        ready(){
          this.songReady = true
          this.savePlayHistory(this.currentSong)
        },
        error(){
          this.songReady = true
        },
        updateTime(e){
          this.currentTime = e.target.currentTime
        },
        format(interval){
          interval = interval | 0
          const minute = interval/60 | 0
          const second = this._pad(interval%60)
          return `${minute}:${second}`
        },
        getLyric(){
              getLyrics(this.currentSong.mid).then((lyric)=>{
              // if(this.currentSong.lyric!==lyric){return}
              this.currentLyric = new Lyric(lyric, this.handleLyric)
              if(this.playing){
                this.currentLyric.play()
              }
              this.state=this.currentLyric.state

            }).catch(() => {
              this.currentLyric = null
              this.playingLyric = ''
              this.currentLineNum = 0
            })

        },
        getLyrics(){
            this.currentSong.getLyrics().then((res)=>{
              console.log(res)
            })
        },
        ...mapActions([
          'savePlayHistory',
          'saveFavoriteList',
          'deleteFavoriteList'
        ]),
        created(){
          this.getLyrics()
        },
        handleLyric({lineNum,txt}){
            this.currentLineNum = lineNum
          if (lineNum > 5) {
            let lineEl = this.$refs.lyricLine[lineNum - 5]
            this.$refs.lyricList.scrollToElement(lineEl, 1000)
          } else {
            this.$refs.lyricList.scrollTo(0, 0, 1000)
          }
          this.playingLyric = txt
        },
        _pad(num,n=2){
            let len = num.toString().length
          while (len<n){
              num = '0' +num
              len++
          }
          return num
        },
        onProgressChange(percent){
          const currentTime = this.currentSong.duration * percent
            this.$refs.audio.currentTime = currentTime
          if (!this.playing){
            this.togglePlaying()
          }
          if (this.currentLyric) {
            this.currentLyric.seek(currentTime * 1000)
          }
        },

        _getPosAndScale() {
          const targetWidth = 40
          const paddingLeft = 40
          const paddingBottom = 30
          const paddingTop = 80
          const width = window.innerWidth * 0.8
          const scale = targetWidth / width
          const x = -(window.innerWidth / 2 - paddingLeft)
          const y = window.innerHeight - paddingTop - width / 2 - paddingBottom
          return {
            x,
            y,
            scale
          }
        },

        ...mapMutations({
          setFullScreen:'SET_FULL_SCREEN',
          setPlayingState:'SET_PLAYING_STATE',
          setCurrentIndex: 'SET_CURRENT_INDEX',
          setPlayMode: 'SET_PLAY_MODE',
          setPlaylist: 'SET_PLAYLIST'
        })
      },
      watch: {
        currentSong(newSong, oldSong) {
          if (!newSong.id) {
            return
          }
          if (newSong.id === oldSong.id) {
            return
          }
          if (this.currentLyric) {
            this.currentLyric.stop()
            this.currentTime = 0
            this.playingLyric = ''
            this.currentLineNum = 0
          }
          clearTimeout(this.timer)
          this.timer = setTimeout(() => {
            this.$refs.audio.play()
            this.getLyric()


          }, 1000)
        },
        playing(newPlaying) {
          const audio = this.$refs.audio
          this.$nextTick(() => {
            newPlaying ? audio.play() : audio.pause()
          })
        },
        fullScreen(newVal) {
          if (newVal) {
            setTimeout(() => {
              this.$refs.lyricList.refresh()
            }, 20)
          }
        },
      }

    }
</script>

