<template>
  <div class="loading"  v-show="result">
    <img width="24" height="24" src="./loading.gif" v-if="hasMore">
    <p class="desc">{{title}}</p>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    props: {
      title: {
        type: String,
        default: '正在载入...'
      },
      hasMore:{
        type:Boolean,
        default: true
      },
      result:{
        type:Number,
        default:1
      }

    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"

  .loading
    width: 100%
    text-align: center
    display: flex
    align-items :center
    justify-content :center
    p
      padding-left :5px
    .desc
      line-height: 20px
      font-size: $font-size-small
      color: $color-text-l
</style>



