<template>
  <Scroll class="suggest" :data="result" :pullup="pullup" @scrollToEnd="searchMore" ref="suggest" :beforeScroll="beforeScroll" @brforeScroll="listenScroll">
    <div class="suggestWrapper" >
  <ul class="suggest-list">
    <li @click="select(item)" class="suggest-item" v-for="item in result">
      <div class="icon">
        <i :class="getIconCls(item)"></i>
      </div>
      <div class="name">
        <p class="text" v-html="getDisplayName(item)"></p>
      </div>
    </li>
    <div class="loading-container" >
      <loading :hasMore="hasMore" :title="title" :result="result.length"></loading>
    </div>
  </ul>
    </div>
    <div class="noResultWrapper" v-show="!result.length&&!hasMore">
      <noResult></noResult>
    </div>
  </Scroll>
</template>

<script>
  import {songSearch} from "../../api/search";
  import {ERR_OK} from "../../api/config";
  import {createSong} from '../../common/js/song'
  import Scroll from  '../../base/scroll'
  import loading from '../../base/loading/loading'
  import Singer from '../../common/js/singer'
  import {mapMutations,mapActions} from 'vuex'
  import noResult from '../../base/noResult'
  const TYPE_SINGER = 'singer'
  const perpage = 20
  export default {
        name: "suggest",
      components:{
        Scroll,
        loading,
        noResult
      },
      props:{
          query:{
            type:String,
            default:''
          },
        showSinger:{
            type:Boolean,
          default: true
        }
      },
    data(){
          return {
            page:1,
            result:[],
            pullup:true,
            hasMore:true,
            title:'正在载入中...',
            beforeScroll:true
          }
    },
      methods:{
        search(){
          this.page = 1
          this.title='正在载入中...'
          this.hasMore = true
          this.$refs.suggest.scrollTo(0,0)
          songSearch(this.query,this.page,this.showSinger,perpage).then((res)=>{
            if(res.code===ERR_OK){
              this.result=this._getResult(res.data)
              this.checkMore(res.data)
            }
          })
        },
        select(item){
          if (item.type === TYPE_SINGER) {
            const singer = new Singer({
              id: item.singermid,
              name: item.singername
            })
            this.$router.push({
              path: `/search/${singer.id}`
            })
            this.setSinger(singer)
          }else {
            this.insertSong(item)
          }
          this.$emit('select')
        },
        ...mapMutations({
          setSinger: 'GET_SINGER'
        }),
        ...mapActions([
          'insertSong'
        ]),
        searchMore(){
          if(!this.hasMore){
            this.title='已加载全部'
            return
          }
          this.page++
          songSearch(this.query, this.page, this.showSinger, perpage).then((res) => {
            if (res.code === ERR_OK) {
              this.result = this.result.concat(this._getResult(res.data))
              this.checkMore(res.data)
            }
          })
        },
        checkMore(data) {
          const song = data.song
          if (!song.list.length || (song.curnum + song.curpage * perpage) > song.totalnum) {
            this.hasMore = false

          }
        },
        _getResult(data){
          let ret=[]
          if(data.zhida&&data.zhida.singerid){
            ret.push({...data.zhida,...{type:TYPE_SINGER}})
          }
          if(data.song){ret  = ret.concat(this._normalizeSongs(data.song.list))}
          return ret
        },
        _normalizeSongs(list) {
          let ret = []
          list.forEach((musicData) => {
            if (musicData.songid && musicData.albummid) {
              ret.push(createSong(musicData))
            }
          })
          return ret
        },
        getIconCls(item){
          if(item.type===TYPE_SINGER){
            return 'iconfont  icon-yonghu'
          }else{
            return 'iconfont icon-yinle'
          }
        },
        getDisplayName(item){
          if (item.type===TYPE_SINGER){
            return item.singername
          } else{
            return `<span style="color:rgba(0,0,0,0.7);margin-bottom: 3px;white-space: nowrap;overflow: hidden;text-overflow:ellipsis;width:300px">${item.name}</span><span class="gray" style="color: rgba(0,0,0,0.3);font-size: 14px;white-space: nowrap;overflow: hidden;text-overflow:ellipsis;width:300px">${item.singer}</span>`
          }
        },
        listenScroll(){
          this.$emit('listScroll')
        },
        refresh(){
            this.$refs.suggest.refresh()
        },

      },
      watch:{
          query(){
            this.search()
          }
      }
    }
</script>

<style scoped lang="stylus">
  .suggest{
    overflow :hidden
    height: 100%
    .suggestWrapper{
      position: relative
      top:0
      overflow:hidden
      .suggest-list{
      .suggest-item{
        display :flex
        align-items :center
        height:55px
        border-bottom:1px solid rgba(0,0,0,0.1)

        .icon{
          flex:0 0 50px
          width:30px
          text-align:center
          i{
            font-size:24px
            color:rgba(0,0,0,0.3)
          }
        }
        .name{
          flex:1
          .text{
            display :flex
            flex-direction :column
            justify-content :center
            span{
              color: #000000
            }
          }

        }
      }
    }
    }
  }
  .loading-container{

    position: relative
    bottom:0
    left: 45%
    margin:10px
    transform :translateX(-50%)

  }

</style>



