<template>
    <div class="tab">
      <router-link to="/recommend">
        <p>推荐</p>
        <span class="target"></span>
      </router-link>
      <router-link to="/singer">
        <p>歌手</p>
        <span class="target"></span>
      </router-link>
      <router-link to="/rank">
       <p>排行</p>
        <span class="target"></span>
      </router-link>
      <router-link to="/search">
        <p>搜索</p>
        <span class="target"></span>
      </router-link>
    </div>
</template>

<script>
    export default {
        name: "tab"
    }
</script>

<style scoped lang="stylus">
  @import '../../common/stylus/variable.styl'
  @import "../../common/stylus/mixin"
  .tab{
    display flex;
    height:44px;
    align-items :center
    font-size: $font-size-medium-x;
  a{
      display :flex;
      flex:1;
      flex-direction: column
      justify-content center;
      align-items :center
    color: rgba(0,0,0,.7)
      height:40px

  }
    .target{
      position: relative;
      top: 0;
      left: 0;
      width: 0;
      height: 4px;
      margin-top: 4px
      transition: 0.2s all linear;
      background-color: #56CBF9
      border-radius :20px
    }


  }
  .router-link-active .target{
    width :30%
    top: 0;
    left: 0;
    transition-delay: 0.1s;
    background-color: #56CBF9
  }
  .router-link-active p{
    color: rgba(0,0,0,.9)
  }

</style>



